body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app > .header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 4rem; }
  .app > .header h2 {
    margin: 0; }

.app .content {
  padding: 0 2rem; }

.home {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }

.habit-list {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-flow: column;
          flex-flow: column;
  max-width: 20rem;
  min-width: 20rem; }

.list {
  padding: 0;
  margin: 0;
  width: 100%; }

.habit {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
  margin-bottom: .75rem; }
  .habit.completed {
    color: lightgrey; }
    .habit.completed .habit-title-and-actions {
      text-decoration: line-through; }

.habit-title-and-actions, .new-habit {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%; }

.current-streak {
  font-size: .75rem; }

